body{
	color: $color-white;
	&.tm-light{
		.b-foot-nav{
			a{
				color: $color-grey7;
			}
		}
		.b-screen{
			&__foot{
				color: $color-grey7;
			}
		}
	}
}
.b-sub-tit {
	color: $color-white;
}
.b-btn{
    &__icon{
		&:before{
			border-color:  $color-white;
		}
		svg{
			stroke: $color-white;
		}
    }
}
.b-btn-update{
	svg{
		fill: $color-white;
	}
}
.b-foot-nav{
	a{
		color: $color-white;
	}
}

// HEADER
.b-header{
    &__logo{
    	width: calc(100% - 610px - 61px - 40px);
    }
    &__login{
    	width: 671px;
    }
}
.b-logo{
	&--lg{
		.b-logo__item{
			background-image: url(../images/svg/logo-white.svg);
		}
	}
}
// BODY
.b-screen {
	&__img{
		width: 610px;
		opacity: 0.4;
	}
	&__txt{
		margin-left: 120px;
		margin-right: 260px;
		&--full{
			margin-right: 0;
			margin-left: 61px;
		}
	}
	&__foot{
		left: 61px;
	}
}