// HEADER
.b-header{
	justify-content: space-between;
    &__logo{
    	width: 50%;
    }
    &__login{
    	width: 500px;
    }
}
.b-lang-nav{
	a{
		color: $color-white;
	}
}
.b-login{
	&__modal{
		padding: 25px 101px 25px 40px;
	}
}
// BODY
.b-screen {
	&__img-sm{
		display: none;
	}
	&__img{
		width: calc(100% - 61px);
		.bg-cover{
			opacity: 0.2;
		}
	}
	&__txt{
		margin-left: 30px;
		margin-right: 120px;
		&--full{
			margin-right: 0;
			margin-left: 61px;
		}
	}
}