@import "variables";
@import "functions";
@import "mixins";
@import "sprites";
@import "fonts";
@import "vendor";
@import "base";
.b-page{
	position: relative;
	overflow: hidden;
	&__header{
		width: 100%;
		position: fixed;
		top: 0;
		left: 0;
		z-index: 10;
	}
	&__body{
		position: relative;
		z-index: 5;
	}
	&__btns-theme{
		transform: translate(0, -50%);
		position: absolute;
		top: 50%;
		right: 20px;
		z-index: 9;
	}
}
.b-btns-theme{
	position: relative;
	&__btn{
		width: 19px;
		height: 22px;
		display: block;
		border: 1px solid $color-grey15;
		background: $color-grey6;
		transition: all 0.3s ease;
		position: relative;
		cursor: pointer;
		.icon{
			width: 11px;
			height: 11px;
			display: block;
			position: absolute;
			top: 50%;
			left: 50%;
		}
		svg{
			width: 100%;
			height: 100%;
			display: block;
			fill: $color-grey15;
			position: absolute;
			top: 0;
			left: 0;
		}
		&:first-child{
			border-radius: 10px 10px 0 0;
			.icon{
				margin: -5px 0 0 -6px;
			}
		}
		&:last-child{
			border-radius: 0 0 10px 10px;
			.icon{
				margin: -6px 0 0 -5px;
			}
		}
		&.is-active{
			background: $color-grey15;
			svg{
				fill: $color-grey6;
			}
		}
		&:disabled{
			cursor: default;
		}
	}
}
// HEADER
.b-header{
	padding: 26px 91px 26px 30px;
	@extend .b-flex;
    @extend .b-flex--middle;
    &__logo{
    	width: 50%;
    }
    &__login{
    	width: calc(50% - 88px);
    	position: absolute;
    	top: 0;
    	right: 0;
    }
    &--ver2{
    	padding-left: 55px;
    	padding-right: 55px;
    	.b-header__logo{
    		display: none;
    	}
    	.b-header__lang{
    		width: 100%;
    		text-align: right;
    	}
    	.b-header__login{
    		display: none;
    	}
    }
}
.b-logo{
	width: 102px;
	height: 29px;
	position: relative;
	&__item{
		width: 100%;
		height: 100%;
		display: block;
		background-position: 0 0;
		background-repeat: no-repeat;
		background-size: 100% 100%;
		text-indent: -9999em;
		overflow: hidden;
		position: absolute;
		top: 0;
		left: 0;
	}
	&--lg{
		width: 160px;
		height: 46px;
	}
}
.b-lang-nav{
	white-space: nowrap;
	font-size: 12px;
	font-weight: 500;
	line-height: 14px;
	text-transform: uppercase;
	position: relative;
	a{
		text-decoration: none;
		&:hover{
			text-decoration: none;
		}
	}
	&__item{
		padding-left: 6px;
		display: inline-block;
		vertical-align: middle;
		&:first-child{
			padding-left: 0;
		}
		&.is-active{
			a{
				text-decoration: none;
			}
		}
	}
}
.b-login{
	position: relative;
	&__btn-login{
		width: 54px;
		height: 14px;
		font-size: 12px;
		font-weight: 500;
		line-height: 14px;
		letter-spacing: 1px;
		transform: rotate(-90deg);
		text-transform: uppercase;
		position: absolute;
		top: 48px;
		right: 4px;
		cursor: pointer;
		z-index: 3;
	}
	&__btn-close{
		width: 22px;
		height: 22px;
		cursor: pointer;
		text-indent: -9999em;
		overflow: hidden;
		position: absolute;
		top: 26px;
		right: 19px;
		z-index: 6;
		&:before,
		&:after{
			height: 1px;
			margin-top: -1px;
			display: block;
			content: '';
			transition: all 0.3s ease;
			position: absolute;
			top: 50%;
			left: 3px;
			right: 3px;
		}
		&:before{
			transform: rotate(-45deg);
		}
		&:after{
			transform: rotate(45deg);
		}
	}
	&__modal{
		width: 100%;
		padding: 35px 121px 35px 60px;
		transition: all 0.8s linear;
		position: absolute;
		top: -600px;
		z-index: 5;
		&:before{
			width: 1px;
			height: 100%;
			display: block;
			content: '';
			opacity: 0.5;
			position: absolute;
			top: 0;
			right: 60px;
		}
		&-btns{
			width: 60px;
			height: 100%;
			cursor: pointer;
			position: absolute;
			top: 0;
			right: 0;
			z-index: 10;
		}
		.b-login__btn-login{
			top: calc(100% - 62px);
		}
	}
	&.is-open{
		.b-login__modal{
			top: 0;
		}
	}
}

// BODY
.b-body{
	min-height: 100vh;
}
.b-logos-list{
	width: 600px;
	height: 25px;
	margin: -17px 0 0 -300px;
	padding: 0 130px;
	white-space: nowrap;
	transform: rotate(-90deg);
	position: absolute;
	top: 50%;
	left: 50%;
	@extend .b-flex;
	@extend .b-flex--middle;
	@extend .b-flex--space-between;
	&__item{
		height: 25px;
	}
	img{
		height: 100%;
		display: block;
		position: relative;
	}
}
.b-captcha{
	position: relative;
	@extend .b-flex;
	@extend .b-flex--middle;
	&__img{
		width: 180px;
		margin-right: 20px;
		display: block;
		img{
			width: 100%;
			display: block;
		}
	}
}
.b-foot-nav{
	a{
		text-decoration: none;
		&:hover{
			text-decoration: none;
		}
	}
	&__item{
		padding-left: 25px;
		display: inline-block;
		vertical-align: middle;
		&:first-child{
			padding-left: 0;
		}
		&.is-active{
			a{
				text-decoration: none;
			}
		}
	}
}
.b-screen {
	opacity: 0;
	.b-logo{
		margin: 0 auto 46px;
	}
	.bg-cover{
		width: 100%;
		height: 100%;
		background-position: center center;
		background-repeat: no-repeat;
		background-size: cover;
		position: absolute;
		top: 0;
		left: 0;
		z-index: 1;
	}
	&__logos{
		width: 60px;
		height: 100%;
		position: absolute;
		top: 0;
		left: 0;
		z-index: 11;
	}
	&__img-sm{
		width: 62px;
		opacity: 0.3;
		overflow: hidden;
		position: absolute;
		top: 80px;
		bottom: 80px;
		left: 0;
		z-index: 5;
	}
	&__img{
		width: calc(50% - 88px - 61px);
		height: 100%;
		overflow: hidden;
		position: absolute;
		top: 0;
		right: 61px;
		z-index: 5;
		span{
			width: 100%;
			height: calc(100% + 5px);
			text-indent: -9999em;
			overflow: hidden;
			opacity: 0.5;
			background-image: url(../images/svg/letter01.svg);
			background-position: right center;
			background-repeat: no-repeat;
			background-size: auto 100%;
			position: absolute;
			top: -4px;
			right: -30px;
			z-index: 10;
		}
		&--left{
			left: 61px;
			right: auto;
			.bg-cover{
				right: 0;
				left: auto;
			}
		}
	}
	&__txt{
		margin: 20px calc(50% - 88px + 40px) 0 120px;
		position: relative;
		overflow: hidden;
		z-index: 9;
		&--full{
			padding: 0px 55px 63px;
			margin-right: 0;
			margin-left: calc(50% - 88px);
			text-align: center;
		}
		.b-tit{
			padding: 22px 0;
		}
		.b-sub-tit{
			padding: 0 0 20px;
		}
		&-etry{
			padding: 16px 0;
			opacity: 0;
			position: relative;
			transform: matrix(1, 0, 0, 1, -200, 0);
		}
		&-foot{
			padding: 16px 0;
			display: inline-block;
			opacity: 0;
			position: relative;
			transform: matrix(1, 0, 0, 1, -200, 0);
		}
		&-form{
			padding: 59px 0 0;
			&--sm{
				padding-top: 23px;
			}
		}
	}
	&__foot{
		padding: 30px 55px;
		font-size: 8px;
		font-weight: 500;
		line-height: 10px;
		letter-spacing: 1px;
		text-transform: uppercase;
		position: absolute;
		bottom: -100px;
		left: calc(50% - 88px);
		right: 0;
		z-index: 9;
		@extend .b-flex;
    	@extend .b-flex--middle;
    	@extend .b-flex--space-between;
	}
	&--ver1{
		.b-screen__img{
			span{
				background-image: url(../images/svg/letter01.svg);
			}
		}
	}
	&--ver2{
		.b-screen__img{
			span{
				background-image: url(../images/svg/letter02.svg);
			}
		}
	}
	&--ver3{
		.b-screen__img{
			span{
				background-image: url(../images/svg/letter03.svg);
			}
		}
	}
	&--ver4{
		.b-screen__img{
			span{
				background-image: url(../images/svg/letter04.svg);
			}
		}
	}
	&--ver5{
		.b-screen__img{
			span{
				background-image: url(../images/svg/letter05.svg);
			}
		}
	}
	&.active{
		.bg-cover{
			animation: cover 1s linear forwards;
		}
		.b-screen__img{
			span{
				animation: cover-txt 1s linear forwards;
			}
			&--left{
				.bg-cover{
					animation: cover-left 1s linear forwards;
				}
			}
		}
		.b-tit{
			span{
				transition: all 0.3s linear;
		        animation: title-txt 0.1s linear backwards;
		        @for $i from 0 through 20 {
		            &:nth-child(#{$i + 1}), {
		                animation-delay: #{($i/10)+0.5}s;
		            }
		        }
			}
		}
		.b-screen__txt{
			&-etry{
				animation: entry-txt 0.5s linear forwards;
				animation-delay: 1s;
			}
			&-foot{
				animation: entry-txt 0.5s linear forwards;
				animation-delay: 1.5s;
			}
			&--full{
				.b-screen__txt-etry{
					animation: entry-txt-right 0.5s linear forwards;
					animation-delay: 1s;
				}
			}
		}
		.b-screen__foot{
			animation: foot-txt 0.5s linear forwards;
			animation-delay: 1.5s;
		}
	}
}
@keyframes cover {
	from {
		left: 100%;
	}
	to {
		left: 0;
	}
}
@keyframes cover-left {
	from {
		right: 100%;
	}
	to {
		right: 0;
	}
}
@keyframes cover-txt {
	from {
		right: -200px;
	}
	to {
		right: -30px;
	}
}
@keyframes entry-txt {
	from {
		opacity: 0;
		transform: matrix(1, 0, 0, 1, -200, 0);
	}
	to {
		opacity: 1;
		transform: matrix(1, 0, 0, 1, 0, 0);
	}
}
@keyframes entry-txt-right {
	from {
		opacity: 0;
		transform: matrix(1, 0, 0, 1, 200, 0);
	}
	to {
		opacity: 1;
		transform: matrix(1, 0, 0, 1, 0, 0);
	}
}
@keyframes title-txt {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}
@keyframes foot-txt {
    0% {
        bottom: -100px;
    }
    100% {
        bottom: 0;
    }
}

@media (max-width: $md) {
    @import 'md';
}

@media (max-width: $sm) {
    @import 'sm';
}

@media (max-width: $xs) {
    @import 'xs';
}

@media (max-width: $xxs) {
    @import 'xxs';
}
