body{
	background: $color-grey1;
	color: $color-grey5;
	.b-tit {
		color: $color-white;
		strong{
			color: $color-orange;
			span{
				color: $color-orange;
			}
		}
		span {
	        color: $color-white;
	    }
	}
	.b-sub-tit {
		color: $color-grey5;
		strong{
			color: $color-orange;
		}
	}
	.b-btn{
	    color: $color-orange;
	    &__icon{
			&:before{
				border-top: 1px solid $color-grey5;
				border-right: 1px solid $color-grey5;
			}
			svg{
				stroke: $color-grey5;
			}
	    }
	    &:hover{
	    	.b-btn__icon{
	    		&:before{
	    			border-color: $color-white;
	    		}
	    	}
	    }
	}
	.b-btn-update{
		svg{
			fill: $color-grey5;
		}
		&:hover{
			svg{
				fill: $color-orange;
			}
		}
	}
	.b-logo{
		&__item{
			background-image: url(../images/svg/logo.svg);
		}
	}
	.b-lang-nav{
		a{
			color: $color-grey5;
			&:hover{
				color: $color-orange;
			}
		}
		&__item{
			&.is-active{
				a{
					color: $color-orange;
				}
			}
		}
	}
	.b-login{
		&__btn-login{
			color: $color-orange;
		}
		&__btn-close{
			&:before,
			&:after{
				background: $color-orange;
			}
		}
		&__modal{
			background: $color-black1;
			&:before{
				background: $color-grey3;
			}
		}
	}
	.b-input{
		border: 1px solid $color-grey3;
		background: $color-black2;
		color: $color-grey5;
		&--light{
			background: $color-grey2;
		}
	}
	#fp-nav {
		li{
			a{
				background: $color-grey5;
				&:hover{
					background: $color-orange;
				}
				&.active{
					background: $color-orange;
				}
			}
		}
	}
	.b-foot-nav{
		a{
			color: $color-grey5;
			&:hover{
				color: $color-orange;
			}
		}
		&__item{
			&.is-active{
				a{
					color: $color-orange;
				}
			}
		}
	}
	.b-screen{
		&__img{
			span{
				background-image: url(../images/svg/letter01.svg);
			}
		}
		&--ver1{
			.b-screen__img{
				span{
					background-image: url(../images/svg/letter01.svg);
				}
			}
		}
		&--ver2{
			.b-screen__img{
				span{
					background-image: url(../images/svg/letter02.svg);
				}
			}
		}
		&--ver3{
			.b-screen__img{
				span{
					background-image: url(../images/svg/letter03.svg);
				}
			}
		}
		&--ver4{
			.b-screen__img{
				span{
					background-image: url(../images/svg/letter04.svg);
				}
			}
		}
		&--ver5{
			.b-screen__img{
				span{
					background-image: url(../images/svg/letter05.svg);
				}
			}
		}
	}
	.b-btns-theme{
		&__btn{
			border: 1px solid $color-grey5;
			background: $color-grey1;
			svg{
				fill: $color-grey5;
			}
			&.is-active{
				background: $color-grey5;
				svg{
					fill: $color-grey1;
				}
			}
		}
	}
	&.tm-light{
		background: $color-grey6;
		color: $color-grey7;
		.b-tit {
			color: $color-grey7;
			strong{
				color: $color-orange;
				span{
					color: $color-orange;
				}
			}
			span {
		        color: $color-grey7;
		    }
		}
		.b-sub-tit {
			color: $color-grey7;
			strong{
				color: $color-orange;
			}
		}
		.b-btn{
		    color: $color-orange;
		    &__icon{
				&:before{
					border-top: 1px solid $color-grey7;
					border-right: 1px solid $color-grey7;
				}
				svg{
					stroke: $color-grey7;
				}
		    }
		    &:hover{
		    	.b-btn__icon{
		    		&:before{
		    			border-color: $color-grey7;
		    		}
		    	}
		    }
		}
		.b-btn-update{
			svg{
				fill: $color-grey7;
			}
			&:hover{
				svg{
					fill: $color-orange;
				}
			}
		}
		.b-logo{
			&__item{
				background-image: url(../images/svg/logo-light.svg);
			}
		}
		.b-lang-nav{
			a{
				color: $color-grey7;
				&:hover{
					color: $color-orange;
				}
			}
			&__item{
				&.is-active{
					a{
						color: $color-orange;
					}
				}
			}
		}
		.b-login{
			&__btn-login{
				color: $color-grey7;
			}
			&__btn-close{
				&:before,
				&:after{
					background: $color-grey7;
				}
			}
			&__modal{
				background: $color-grey8;
				&:before{
					background: $color-grey9;
				}
			}
		}
		.b-input{
			border: 1px solid $color-grey10;
			background: $color-grey8;
			color: $color-grey7;
			&--light{
				background: $color-grey13;
			}
		}
		#fp-nav {
			li{
				a{
					background: $color-grey12;
					opacity: 1;
					&:hover{
						background: $color-orange;
					}
					&.active{
						background: $color-orange;
					}
				}
			}
		}
		.b-foot-nav{
			a{
				color: $color-grey14;
				&:hover{
					color: $color-orange;
				}
			}
			&__item{
				&.is-active{
					a{
						color: $color-orange;
					}
				}
			}
		}
		.b-screen{
			&__foot{
				color: $color-grey14;
			}
			&__img{
				span{
					background-image: url(../images/svg/letter01l.svg);
				}
			}
			&--ver1{
				.b-screen__img{
					span{
						background-image: url(../images/svg/letter01l.svg);
					}
				}
			}
			&--ver2{
				.b-screen__img{
					span{
						background-image: url(../images/svg/letter02l.svg);
					}
				}
			}
			&--ver3{
				.b-screen__img{
					span{
						background-image: url(../images/svg/letter03l.svg);
					}
				}
			}
			&--ver4{
				.b-screen__img{
					span{
						background-image: url(../images/svg/letter04l.svg);
					}
				}
			}
			&--ver5{
				.b-screen__img{
					span{
						background-image: url(../images/svg/letter05l.svg);
					}
				}
			}
		}
		.b-btns-theme{
			&__btn{
				border: 1px solid $color-grey15;
				background: $color-grey6;
				svg{
					fill: $color-grey15;
				}
				&.is-active{
					background: $color-grey15;
					svg{
						fill: $color-grey6;
					}
				}
			}
		}
	}
}
