body{
	line-height: 22px;
}
#fp-nav {
    &.fp-right{
    	right: 12px;
    }
    &.fp-left {
	    left: 12px;
	}
}
.b-fild-form{
	padding: 10px 0;
	&--row{
		margin: 0;
		padding: 0;
		display: block;
	}
	&__col{
		width: auto;
		padding: 10px 0;
		text-align: center;
		justify-content: center;
		&--right{
			justify-content: center;
		}
	}
}
.b-input{
	height: 40px;
	padding: 10px;
	line-height: 20px;
}
.b-btn{
	&__icon{
    	width: 40px;
		height: 40px;
	}
}
.b-tit {
	font-size: 24px;
	line-height: 30px;
	span {
		min-width: 10px;
    }
}
.b-sub-tit {
	font-size: 14px;
	line-height: 20px;
}
.b-page{
	&__btns-theme{
		right: 5px;
	}
}
// HEADER
.b-header{
	padding: 15px 51px 15px 20px;
    &__logo{
    	width: 50%;
    }
    &__login{
    	width: 100%;
    }
    &--ver2{
    	padding-left: 20px;
    	padding-right: 20px;
    }
}
.b-logo{
	&__item{
		background-image: url(../images/svg/logo-white.svg);
	}
	&--lg{
		width: 102px;
		height: 29px;
	}
}
.b-login{
	&__btn-login{
		top: 40px;
		right: -10px;
	}
	&__btn-close{
		right: 5px;
	}
	&__modal{
		padding: 20px 51px 10px 20px;
		&:before{
			right: 30px;
		}
		.b-login__btn-login{
			top: calc(100% - 55px);
		}
	}
}
// BODY
.b-logos-list{
	width: 300px;
	height: 20px;
	margin: -10px 0 0 -150px;
	padding: 0 30px;
	&__item{
		height: 20px;
	}
}
.b-captcha{
	justify-content: center;
}
.b-screen {
	.b-logo{
		margin-bottom: 20px;
	}
	&__logos{
		width: 30px;
	}
	&__img-sm{
		display: none;
	}
	&__img{
		width: calc(100% - 31px);
		right: 31px;
		&--left{
			left: 31px;
		}
	}
	&__txt{
		margin-top: 30px;
		margin-left: 20px;
		margin-right: 40px;
		&--full{
			margin-right: 0;
			margin-left: 31px;
			padding: 0px 20px 50px;
		}
		.b-tit{
			padding: 12px 0;
		}
		.b-sub-tit{
			padding: 0 0 10px;
		}
		&-etry{
			padding: 10px 0;
		}
		&-foot{
			padding: 10px 0;
		}
		&-form{
			padding: 10px 0 0;
		}
	}
	&__foot{
		padding: 5px 20px;
		display: block;
		text-align: center;
		left: 31px;
		&-col{
			padding: 5px 0;
		}
	}
}