html.fp-enabled,
.fp-enabled body {
    margin: 0;
    padding: 0;
    overflow:hidden;
    -webkit-tap-highlight-color: rgba(0,0,0,0);
}
.fullpage-wrapper {
    width: 100%!important;
    transform: none!important;
}
.fp-section {
    width: 100%!important;
    position: absolute;
    left: 0;
    top: 0;
    visibility: hidden;
    opacity: 0;
    z-index: 0;
    transition: all .7s ease-in-out;
    &.active {
        visibility: visible;
        opacity: 1;
        z-index: 1;
    }
}
.fp-slide {
    float: left;
}
.fp-slide, .fp-slidesContainer {
    height: 100%;
    display: block;
}
.fp-slides {
    z-index:1;
    height: 100%;
    overflow: hidden;
    position: relative;
    transition: all 0.3s ease-out;
}
.fp-section.fp-table, .fp-slide.fp-table {
    table-layout:fixed;
    width: 100%;
    display: table;
}
.fp-tableCell {
    width: 100%;
    height: 100%;
    display: table-cell;
    vertical-align: middle;
}
.fp-slidesContainer {
    float: left;
    position: relative;
}
.fp-scrollable {
	min-height: 100vh;
    overflow: hidden;
    position: relative;
}
.fp-scroller{
    overflow: hidden;
}
.iScrollIndicator{
    border: 0 !important;
}
.fp-notransition {
    -webkit-transition: none !important;
    transition: none !important;
}
#fp-nav {
    position: fixed;
    z-index: 100;
    bottom: 30px;
    opacity: 1;
    &.fp-right{
    	right: 26px;
    }
    &.fp-left {
	    left: 26px;
	}
	li{
		padding: 13px 0 0;
		position: relative;
		a{
			width: 8px;
			height: 8px;
			display: block;
			background: $color-grey5;
            border-radius: 50%;
            opacity: 0.2;
			transition: all 0.3s ease;
			position: relative;
			&:hover{
				background: $color-orange;
                opacity: 1;
			}
			&.active{
				background: $color-orange;
                opacity: 1;
			}
			span{
				display: none;
			}
		}
	}
}
.no-nav{
    #fp-nav{
        display: none;
    }
}
.fp-auto-height.fp-section,
.fp-auto-height .fp-slide,
.fp-auto-height .fp-tableCell{
    height: auto !important;
}

.fp-responsive .fp-auto-height-responsive.fp-section,
.fp-responsive .fp-auto-height-responsive .fp-slide,
.fp-responsive .fp-auto-height-responsive .fp-tableCell {
    height: auto !important;
}

.iScrollVerticalScrollbar{
	opacity: 0;
}