.b-fild-form{
	padding: 15px 0;
	text-align: left;
	position: relative;
	&--row{
		margin: 0 -15px;
		display: flex;
	    flex-direction: row;
	    flex-wrap: nowrap;
	    justify-content: space-between;
	    align-content: stretch;
	    align-items: center;
	}
	&--bot{
		padding-top: 42px;
	}
	&__col{
		width: 50%;
		padding: 0 15px;
		&--right{
			display: flex;
		    flex-direction: row;
		    flex-wrap: nowrap;
		    justify-content: flex-start;
		    align-content: stretch;
			justify-content: flex-end;
		}
	}
}
.b-input{
	width: 100%;
	height: 48px;
	padding: 10px 20px;
	border-radius: 6px;
	font-size: 12px;
	line-height: 28px;
	position: relative;
}

.b-btn{
	display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: flex-start;
    align-content: stretch;
    align-items: center;
    border: none;
    background: none;
    font-size: 12px;
    font-weight: 500;
    text-transform: uppercase;
    text-decoration: none;
    cursor: pointer;
    position: relative;
    &__txt{
    	padding-right: 20px;
		transition: padding 0.6s cubic-bezier(0.23, 1, 0.32, 1);
    }
    &__icon{
    	width: 60px;
		height: 60px;
		position: relative;
		&:before{
			width: 8px;
			height: 8px;
			margin: -4px 0 0 -6px;
			display: block;
			content: '';
			transform: rotate(45deg);
			transition: transform 0.6s cubic-bezier(0.23, 1, 0.32, 1);
			position: absolute;
			top: 50%;
			left: 50%;
		}
		svg{
			width: 100%;
			height: 100%;
			fill: none;
			position: absolute;
			top: 0;
			left: 0;
			path {
				&.topcircle {
					stroke-width: 2;
					transition: stroke-dashoffset 1s cubic-bezier(0.23, 1, 0.32, 1);
					stroke-dasharray: 222;
					stroke-dashoffset: 222;
					animation: dashOut 1s cubic-bezier(0.23, 1, 0.32, 1) forwards;
				}
			}
		}
    }
    &:hover{
    	text-decoration: none;
    	.b-btn__txt{
    		padding-left: 20px;
    		padding-right: 0;
    	}
    	.b-btn__icon{
    		svg{
    			path{
    				animation: dashIn 1s cubic-bezier(0.23, 1, 0.32, 1) forwards;
    			}
    		}
    	}
    }
}
@keyframes dashIn {
	from {
		stroke-dashoffset: 222;
	}
	to {
		stroke-dashoffset: 0;
		stroke: $color-orange;
	}
}
@keyframes dashOut {
	from {
		stroke-dashoffset: 0;
	}
	to {
		stroke-dashoffset: -222;
		stroke: $color-orange;
	}
}
.b-btn-update{
	width: 18px;
	height: 18px;
	display: block;
	border: none;
	background: none;
	text-indent: -9999em;
	overflow: hidden;
	position: relative;
	cursor: pointer;
	svg{
		width: 100%;
		height: 100%;
		display: block;
		fill: $color-grey5;
		transition: all 0.3s ease;
		position: absolute;
		top: 0;
		left: 0;
	}
	&:hover{
		svg{
			fill: $color-orange;
		}
	}
}