*,
*::before,
*::after {
    box-sizing: border-box;
}
html,
body {
    width: 100%;
    min-height: 100%;
    padding: 0;
    margin: 0;
    font-family: $font-family-mont;
    font-size: 14px;
    font-weight: 300;
    line-height: 24px;
    position: relative;
}
div,
ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6,
form,
select,
input,
textarea,
img,
dl,
dt,
dd,
table,
div,
td,
theder,
button,
blockquote,
article,
aside,
audio,
canvas,
command,
datalist,
details,
figcaption,
figure,
footer,
header,
hgroup,
keygen,
mark,
menu,
meter,
main,
nav,
output,
progress,
rp,
rt,
ruby,
section,
source,
summary,
time,
video,
wbr {
    padding: 0;
    margin: 0;
}
article,
aside,
audio,
canvas,
command,
datalist,
details,
figcaption,
figure,
footer,
header,
hgroup,
keygen,
mark,
menu,
main,
meter,
nav,
output,
progress,
rp,
rt,
ruby,
section,
source,
summary,
time,
video,
wbr,
address {
    display: block;
    font-style: normal;
}
ul,
ol,
li {
    list-style: none;
}
*:active,
*:focus,
a,
input,
textarea,
button {
    outline: none !important;
    -moz-appearance: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    -webkit-appearance: none;
    appearance: none;
    box-shadow: none;
}
input,
textarea,
button{
    font-family: $font-family-mont;
    font-weight: 300;
    border-radius: 0;
}
textarea{
	resize: none;
}
::-moz-focus-inner{
    border: none;
}
a {
	color: $color-orange;
    transition: all 0.3s ease;
    text-decoration: none;
    &:hover{
        text-decoration: underline;
    }
}
strong{
	font-weight: 500;
}
::-webkit-input-placeholder {
    opacity: 0.5;
    transition: opacity 0.3s ease;
}
::-moz-placeholder {
    opacity: 0.5;
    transition: opacity 0.3s ease;
}
:-moz-placeholder {
    opacity: 0.5;
    transition: opacity 0.3s ease;
}
:-ms-input-placeholder {
    opacity: 0.5;
    transition: opacity 0.3s ease;
}
:focus {
    &::-webkit-input-placeholder {
        opacity: 0;
        -moz-transition: opacity 0.3s ease;
    }
}
:focus {
    &::-moz-placeholder {
        opacity: 0;
        -moz-transition: opacity 0.3s ease;
    }
}
:focus {
    &:-moz-placeholder {
        opacity: 0;
        -moz-transition: opacity 0.3s ease;
    }
}
:focus {
    &:-ms-input-placeholder {
        opacity: 0;
        transition: opacity 0.3s ease;
    }
}
.cf {
    &:before,
    &:after {
        content: '';
        display: block;
        overflow: hidden;
    }
    &:after {
        clear: both;
    }
}
.b-flex {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: flex-start;
    align-content: stretch;
    &--middle {
        align-items: center;
    }
    &--space-between {
        justify-content: space-between;
    }
    &--bottom {
        align-items: flex-end;
    }
    &--end,
    &--end-col {
        align-items: flex-end;
    }
    &--middle-col,
    &--bottom-col,
    &--end-col,
    &--top-col {
        flex-direction: column;
    }
    &--middle-col {
        justify-content: center;
    }
    &--bottom-col {
        justify-content: flex-end;
    }
    &--top-col {
        justify-content: flex-start;
    }
    &--right {
        justify-content: flex-end;
    }
    &--center {
        justify-content: center;
    }
}
.b-center {
	max-width: 1150px;
	margin: 0 auto;
	padding: 0 20px;
    position: relative;
    z-index: 10;
}
.b-tit {
	font-size: 48px;
	font-weight: 700;
	line-height: 57px;
	text-transform: uppercase;
	strong{
		display: block;
		font-weight: 700;
	}
	span {
		min-width: 16px;
		display: inline-block;
		vertical-align: middle;
    }
}
.b-sub-tit {
	font-size: 16px;
	font-weight: 700;
	line-height: 22px;
	text-transform: uppercase;
	color: $color-grey5;
	strong{
		font-weight: 700;
		color: $color-orange;
	}
}

.b-wrapper{
	.b-tit{
		padding: 22px 0;
	}
	&__etry{
		padding: 16px 0;
	}
	&__foot{
		padding: 16px 0;
		display: inline-block;
	}
}